/* ======== MEDIA QUERY(Small Devices) ======= */
@media screen and (max-width: 600px) {
  /* header{
    height:100vh;
} */

  .about__me {
    width: 65%;
    margin: 0 auto 3rem;
  }

  .about__cards {
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
  }

  .about__content {
    text-align: center;
  }

  .about__content p {
    margin: 1.5rem 0;
  }
  .about__me-Alex {
    border-radius: 2rem;
    height: 18rem;
  }
}
